#svg-container {
  display: inline-grid;
  grid-template-columns: repeat(4, 1fr);
  max-width: 1200px;
  margin: 1.5em 0;
}

svg.chart {
  grid-column: 2 / span 3;
  width: 100%;
}

@media (max-width: 1200px) {
  svg.chart {
    grid-column: 1 / span 4;
  }  
}


